@import 'tabulator-dark-mode.scss';

body {
	margin: 4px 4px 4px 4px;
}

.thetop {
	height: 24px;
}

.main-window {
	height: calc(100vh - 4px - 4px - 24px - 40px);
}

/* ------------------------------------------------------ */

.tabulator-col.tabulator-col-vertical.tabulator-sortable.tabulator-col-sorter-element .tabulator-col-title::before {
	background-repeat: no-repeat;
	background-position-y: center;
}

.tabulator-col .tabulator-col-title::before {
	content: '';
	width: 32px;
	height: 32px;
	background-size: 32px;
	margin-bottom: 3px;
}

/* ------------------------------------------------------ */

.scrolltop {
	display: none;
	width: 100%;
	margin: 0 auto;
	position: fixed;
	bottom: 10px;
	right: 10px;
}

.scroll {
	position: absolute;
	right: 0px;
	bottom: 0px;
	padding: 5px;
	text-align: center;
	margin: 0 0 0 0;
	cursor: pointer;
}

.scroll .fa-solid {
	font-size: 28px;
}

/* ------------------------------------------------------ */